import React, { Component } from 'react'
import Trie from 'merkle-patricia-tree'
import Button from '../../../widgets/Button'
import Tree from '../../../widgets/Tree'

export default class TrieViz extends Component {
  state = {
    trie: undefined,
    data: {
      name: 'root',
      children: []
    }
  }
  componentDidMount = () => {
    this.setState({
      trie: new Trie()
    })
  }
  add = async () => {
    const { trie } = this.state
    const put = (key, val) => new Promise((resolve, reject) => {
      trie.put(key, val, (error, data) => {
        if(error) return reject(error)
        return resolve(data)
      })
    })
    
    await put(Buffer.from('test'), Buffer.from('one'))
    await put(Buffer.from('foo'), Buffer.from('boz'))
    await put(Buffer.from('bar'), Buffer.from('baz'))
    const value = await new Promise((resolve, reject) => {
      trie.get(Buffer.from('test'), (error, data) => {
        if(error) return reject(error)
        return resolve(data)
      })
    })

    console.log(trie._root.toString('hex'))
    const onNode = (nodeRef, node, key, walkController) => {
      console.log('on node called', nodeRef.toString('hex'), node, key)
      walkController.next()
    }
    const onDone = () => {
      console.log('on done called')
    }
    trie._walkTrie(trie._root, onNode, onDone)

    console.log('transform trie', trie)

    console.log(value.toString())
  }
  render() {
    const { data } = this.state
    return (
      <div style={{
        border: '1px solid black'
      }}>
        <Button label="add" onClick={this.add}/>
        <Tree data={data} />
      </div>
    )
  }
}
