const fetchJson = async (_url) => {
  const data = await fetch(_url)
  return data.json()
}

const request = async (url, data) => {
  const response = await fetch(url, {
    method: 'POST', // *GET, POST, PUT, DELETE, etc.
    body: JSON.stringify(data)
  })
  return response.json();
}

let i = 0

class Grid {
  constructor() {
    
  }
  async getVersionList() {
    let versionList = await fetchJson('http://localhost:4743/clients')
    return versionList
  }
  async searchWorkflows(query) {
    let workflows = await request('http://localhost:4743/rpc', {
      method: 'searchWorkflows',
      params: [query],
      id: i++
    })
    return workflows
  }
  async runWorkflow(workflowId) {
    await request('http://localhost:4743/rpc', {
      method: 'runWorkflow',
      params: [workflowId],
      id: i++
    })
  }
  async getJobs() {
    let jobs = await request('http://localhost:4743/rpc', {
      method: 'getJobs',
      params: [],
      id: i++
    })
    return jobs
  }
}

export default new Grid()